.verifyBar {
    margin-top : 90px;
    text-align : center
}
.verifyButton {
    color : white;
    padding : 10px;
    background-color:  #07323f;
    margin : 12px 0;
}
.details {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50vh;
}

.quizForm {
    padding: 30px;
    box-shadow: 2px 2px 2px grey, -2px -2px 2px grey;
}

.quizForm div {
    margin-bottom: 10px;
}

.quizForm input {
    background-color: black;
    border: 1px solid white;
    color: white;
    padding: 7px;
    margin-left: 10px;
    width: 200px
}
.quizForm input[type=number] {
    width: 50px;
}
.head {
    text-align : center;
    font-weight : bold; 
    color : rgba(0, 0, 0, 0.7);
}
.quizForm button {
    display: block;
    width: 100px;
    margin: auto;
    padding: 10px;
    color: white;
    background-color: black
}

.button {
    padding: 12px;
    display: block;
    margin: 30px auto;
    width: 120px;
}

.quiz {
    padding-top: 95px;
}

.complete { 
    display: flex;
    align-items: center;
    height: 50vh;
    justify-content: center;
    text-align: center;
    padding: 20px;
}

.complete a {
    color: burlywood;
    word-wrap: break-word;
}

.btnCopy {
    display: block;
    margin: auto;
    margin-top: 10px;
    padding: 7px;
    background-color: darkgray;
    color: white;
    border-radius: 12px;
    transition: opacity 1.3s;
}